	font-size: 20px !important;
	font-weight: 500;
	///letter-spacing: 0.15em;
}
[subtitle1] {
	font-size: 16px;
	font-weight: 400;
	//letter-spacing: 0.15em;
}
[subtitle2] {
	font-size: 14px;
	font-weight: 500;
	//letter-spacing: 0.1em;
}

[body1] {
	font-size: 16px;
	font-weight: 400;
	//letter-spacing: 0.5em;
}
// NOTE: replaces [secondary] from previous flexus version
[body2] {
	font-size: 14px;
	font-weight: 400;
	//letter-spacing: 0.25em;
	// This mixin goes beyond the font-size and adds color.
	// But according to Material Design.
	//&:extend([muted]);
}
[button] {
	font-family: var(--font-family);
	font-size: 14px;
	font-weight: 500;
	//letter-spacing: 0.75em;
	letter-spacing: .25px;
	text-transform: uppercase;
	-webkit-font-smoothing: antialiased;
}

[caption] {
	font-size: 12px;
	font-weight: 400;
	//letter-spacing: 0.4em;
}
[overline] {
	font-size: 10px;
	font-weight: 400;
	//letter-spacing: 1.5em;
	text-transform: uppercase;
}


